.grid {
  background-color: aqua;
  height: 120vh;
  width: 100vw;
  /* padding-inline: 0rem 0.5rem; */
  display: flex;
  color: aliceblue;
  justify-content: space-between;
}

h4 {
  margin-block: 0.5rem;
}

.grid>*:nth-child(n) {
  flex-basis: 100%;
}

.grid>*:nth-child(1) {
  flex: 0 0 10%;
  background-color: #525252;
}

.grid>*:nth-child(2) {
  flex: 0 0 50%;
  background-color: hsl(225, 6%, 25%);
}

.grid>*:nth-child(3) {
  flex: 0 0 40%;
  background-color: #222831;
}

.nav {
  height: 5vh;
  display: flex;
  padding: 2% 1% 0% 0.5%;
  background-color: hsl(225, 6%, 13%);
}

.nav>*:nth-child(n) {
  flex-basis: 50%;
}

.nav-left {
  display: flex;
  text-align: left;
  justify-content: space-around;
  /* background-color: aqua; */
}

.nav-left>*:nth-child(n) {
  flex-basis: 100%;
}

.nav-left>*:nth-child(2) {
  margin: 0;
}

.nav-right {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-evenly;
  /* background-color: aqua; */
  margin-left: 60%;
}

.files {
  display: flex;
  flex-wrap: wrap;

  /* background-color: rgb(131, 211, 155); */
}

.files>*:nth-child(n) {
  flex-basis: 100%;
  margin: 1rem;
  align-items: top;
  justify-content: first baseline;
  margin: 1rem 1rem;
  cursor: pointer;
}

button {
  background-color: transparent;
  border: none;
  color: aliceblue;
  padding: 0.2rem;
  margin-inline: 0.4rem;
}

.editor-title {
  display: flex;
  /* justify-content: space-between; */
  background-color: hsl(220, 2%, 24%);
  margin: 0;
  padding: 0.3rem 0.5rem 0.5rem 1rem;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
  z-index: 1;
}

.editor-title:active {
  display: flex;
  /* justify-content: space-between; */
  background-color: hsl(240, 4%, 10%);
  margin: 0;
  padding: 0.3rem 0.5rem 0.5rem 1rem;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
  z-index: 10;
}

.editor-container {
  object-fit: contain;
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: 0.2vh;
  background-color: hsl(240, 4%, 10%);
  height: 112vh;
  color: #59caa8;
}

.code-mirror-wrapper {
  flex-grow: inherit;
  border-radius: 2vh;
}

.Code_screen {
  border: 2px red solid;
  flex: 0.4;
}

.CodeMirror {
  height: 100% !important;
  max-height: 108vh;
}


.output_screen {
  /* margin-left: 10px; */
  /* flex: 1; */
  /* align-self: stretch; */
  height: 110vh;
}

.output {
  background-color: aliceblue;
  height: 99vh;
  margin: 0.5vh;
  text-align: center;
  align-content: center;
  color: rgb(16, 175, 122);
  border-radius: 12px;
  font-weight: 900;
  /* padding-bottom: 25%; */
}